import { mapGetters } from "vuex";

export default {
  props: ["nowPlayWayInfo"],
  data() {
    return {
      /* numberList: [
        {
          type: '万位',
          isSelectAll: false,
          isSelectBig: false,
          isSelectSmall: false,
          isSelectOdd: false,
          isSelectEven: false,
          numberList: [
            { isSelected: false, value: 0, label: 0 },
            { isSelected: false, value: 1, label: 1 },
            { isSelected: false, value: 2, label: 2 },
            { isSelected: false, value: 3, label: 3 },
            { isSelected: false, value: 4, label: 4 },
            { isSelected: false, value: 5, label: 5 },
            { isSelected: false, value: 6, label: 6 },
            { isSelected: false, value: 7, label: 7 },
            { isSelected: false, value: 8, label: 8 },
            { isSelected: false, value: 9, label: 9 }
          ],
          ylList: [5, 5, 17, 11, 14, 13, 0, 2, 20, 6],
          lrList: [14, 11, 10, 16, 18, 9, 10, 14, 5, 15]
        }
      ] */
    };
  },
  computed: {
    ...mapGetters([""])
  },
  methods: {
    setNowPlayWayInfo() {
      const nowPlayWayInfo = this.nowPlayWayInfo;
      if (
        !nowPlayWayInfo ||
        !nowPlayWayInfo["ball_strings"] ||
        !nowPlayWayInfo["ball_values"]
      ) {
        return false;
      }
      let ballStrings = nowPlayWayInfo["ball_strings"].split("|");
      let ballValues = nowPlayWayInfo["ball_values"].split("|");
      if (ballStrings.length === ballValues.length && ballValues.length === 1) {
        ballStrings = ballStrings[0].split(",");
        ballValues = ballValues[0].split(",");
        this.numberList.map(item => {
          item.numberList.map((itemInner, index) => {
            itemInner.isSelected = false;
            itemInner.value = ballValues[index];
            itemInner.label = ballStrings[index];
          });
        });
      } else {
        // 如果拆分为多个格式情况
        ballStrings = ballStrings.map(numberItem => {
          return numberItem.split(",");
        });
        ballValues = ballValues.map(numberItem => {
          return numberItem.split(",");
        });
        this.numberList.map((item, index) => {
          item.numberList.map((itemInner, indexInner) => {
            itemInner.isSelected = false;
            itemInner.label = ballStrings[index][indexInner];
            itemInner.value = ballValues[index][indexInner];
          });
        });
      }
    }
  },
  created() {
    // console.log(this.nowPlayWayInfo)
    this.setNowPlayWayInfo();
  }
};
